import React from "react";
import style from './Menu.css'
import MenuDesenho from '../imagens/menu.png'
import {Link} from 'react-router-dom'
import { useState } from "react";

export default function Menu() {

    const [showMenu, setMenu] = useState('false');

    function toggleMenu() {
        setMenu(!showMenu);
      };

    return (
        <div className="container">
           <div id="menu-section" className={showMenu ? "active" : null}>
            <button id="btn-menu" onClick={toggleMenu}>Menu
            <span id="hamburguer"></span>
            </button>
                <div id="linkContainer" className={showMenu ? "active" : "inactive"}>
                    <Link to="/cowboysite"><button id="botaoMenu" className={showMenu ? "active" : null}>Home</button></Link>
                    <Link to="/Videos"><button id="botaoMenu" className={showMenu ? "active" :null}>Vídeos</button></Link>
                    <Link to="/HistoriaFilmagens"><button id="botaoMenu" className={showMenu ? null : null}>História Filmagens</button></Link>
                    <Link to="/HistoriaCowboy"><button id="botaoMenu" className={showMenu ? "active" : null}>História Cowboy</button></Link>
                    <Link to="/Musicas"><button id="botaoMenu" className={showMenu ? "active" : null}>Músicas</button></Link>
                </div>
           </div>
        </div>
    )
}